<template>
  <div class="market">
    <v-row class="mt-5">
      <v-col class="bulletin-col" cols="12">
        <h1>市場動態</h1>

        <table class="table mt-5" style="width: 100%">
          <thead>
            <tr>
              <th>名稱</th>
              <th>最新價格</th>
              <th>24小時漲跌</th>
              <th v-if="screenWidth > 768">行情圖</th>
              <th v-if="screenWidth > 768"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(coin, index) in tableCoin" :key="index">
              <th>
                {{ coin.symbol }}
                <span style="color: #747474">{{ coin.name }} </span>
              </th>
              <th>${{ coin.marketData.currentPrice.usd }}</th>
              <th :class="classList(coin.marketData.priceChange)">
                {{ coin.marketData.priceChange["24h"].percentage.usd }}%
              </th>
              <th v-if="screenWidth > 768">
                <Chart
                  :size="{ width: 250, height: 80 }"
                  :data="coin.history_data"
                  direction="horizontal"
                  :axis="{
                    secondary: {
                      domain: [coin.history_min, coin.history_max],
                      type: 'linear',
                    },
                  }"
                >
                  <template #layers>
                    <Line
                      :dataKeys="['name', 'pl']"
                      :lineStyle="{ stroke: 'red' }"
                      type="normal"
                    />
                  </template>
                </Chart>
              </th>
              <th v-if="screenWidth > 768"><v-btn> 購買 </v-btn></th>
            </tr>
          </tbody>
        </table>
      </v-col>
      <v-col cols="12" class="text-center">
        <span
          style="cursor: pointer; font-size: 14px; color: #747474"
          class="mr-2"
        >
          查看更多
        </span>
        <span style="cursor: pointer; font-size: 14px; color: #747474">
          <i class="fa-solid fa-chevron-right"></i>
        </span>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { Chart, Line } from "vue3-charts";

export default {
  name: "market_f",
  props: ["tableCoin", "screenWidth"],
  components: { Chart, Line },
  data: () => ({
    axis: {
      secondary: {
        domain: ["dataMin", "dataMax+0.5"],
        type: "linear",
        ticks: 8,
      },
    },
  }),
  methods: {
    classList(item) {
      const usd_percentage = item["24h"].percentage.usd;
      if (usd_percentage > 0) {
        return "text-green";
      } else {
        return "text-red";
      }
    },
  },

  mounted() {},
};
</script>
<style lang="scss" >
.layer-axis-x {
  .tick {
    display: none;
  }
}
</style>