<template>
  <div>
    <HEADER :screenWidth="screenWidth" />
  </div>
  <BANNER :tableCoin="tableCoin" :screenWidth="screenWidth" />
  <v-container>
    <INSTRUCTION :screenWidth="screenWidth" />
    <div class="">
      <MARKET :tableCoin="tableCoin" :screenWidth="screenWidth" />
      <NFT :screenWidth="screenWidth" />
      <CONTACT :screenWidth="screenWidth" />
      <QA :screenWidth="screenWidth" />
      <COMMUNITY :screenWidth="screenWidth" />
    </div>
  </v-container>
  <v-container fluid>
    <FOOTER :screenWidth="screenWidth" />
  </v-container>
</template>

<script>
import axios from "axios";

import HEADER from "./HEADER.vue";
import BANNER from "./BANNER.vue";
import INSTRUCTION from "./INSTRUCTION.vue";
import MARKET from "./MARKET.vue";
import NFT from "./NFT.vue";
import CONTACT from "./CONTACT.vue";
import QA from "./QA.vue";
import COMMUNITY from "./COMMUNITY.vue";
import FOOTER from "./FOOTER.vue";

export default {
  name: "HelloWorld",

  components: {
    HEADER,
    BANNER,
    INSTRUCTION,
    MARKET,
    NFT,
    CONTACT,
    QA,
    COMMUNITY,
    FOOTER,
  },

  data: () => ({
    screenWidth: 1920,
    tableCoin: [],
  }),
  computed: {},
  methods: {
    async getCoin() {
      const res = await axios(
        "https://exchange-price-crawler-d74kfej72a-de.a.run.app/coins"
      );
      this.tableCoin = res.data.map((item) => {
        item.history_f = item.history.slice(
          item.history.length - 20,
          item.history.length
        );
        item.history_data = item.history_f.map((history, index) => {
          return {
            name: index,
            pl: history,
          };
        });
        item.history_min = Math.min(...item.history_f);
        item.history_max = Math.max(...item.history_f);
        return item;
      });
    },
  },

  mounted() {
    this.getCoin();
    this.screenWidth = document.documentElement.clientWidth;
  },
};
</script>
<style lang="scss" >
.carousel__pagination-button--active {
  background-color: #e79e64 !important;
}
.carousel__pagination-button {
  background-color: #b6b6b6;
}
.NFT_carousel {
  position: relative;
  width: 100%;
  height: 460px;
  background-image: url("../assets/NFT.png");
  background-position: center;
  .title {
    position: absolute;
    bottom: 100px;
    left: 10%;
    width: 600px;
    padding: 2.5rem 1rem;
    background: rgba(88, 88, 88, 0.6);
    color: #fff;
  }
}
</style>