<template>
  <div class="banner">
    <div>
      <v-container>
        <v-row>
          <v-col cols="12" class="banner-title">
            <h1>CBD交易數位資產</h1>
            <h1>方便 快速</h1>
            <p class="mt-4">全球最佳的區塊鏈資產交易平台。</p>
            <v-btn
              class="mt-4"
              style="
                width: 250px;
                background: #e79e64;
                font-weight: bold;
                color: black;
              "
              color="primary"
            >
              立即註冊
            </v-btn>
          </v-col>
        </v-row>
        <v-row v-if="screenWidth < 768">
          <v-col v-for="(coin, index) in tableCoin" :key="index" cols="4">
            <span class="mr-1">
              {{ coin.symbol }}
            </span>
            <span
              style="font-size: 8px"
              :class="classList(coin.marketData.priceChange)"
            >
              {{ coin.marketData.priceChange["24h"].percentage.usd }}%
            </span>
            <h4>
              {{ coin.marketData.currentPrice.usd }}
            </h4>
            <span style="font-size: 8px">
              $ {{ coin.marketData.currentPrice.usd }}
            </span>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "BANNER",
  props: ["tableCoin", "screenWidth"],

  data: () => ({}),
  methods: {
    classList(item) {
      const usd_percentage = item["24h"].percentage.usd;
      if (usd_percentage > 0) {
        return "text-green";
      } else {
        return "text-red";
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.banner {
  height: 637px;
  background-image: url("../assets/logo_mockup.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #fff;

  h1 {
    font-size: 52px;
  }
  p {
    font-size: 16px;
  }
  .banner-title {
    position: relative;
    top: 100px;
    left: 70px;
    width: 100%;
  }
  @media all and (max-width: 767px) {
    background-image: none;
    color: #000;
    height: auto;
    h1 {
      font-size: 28px;
    }
    p {
      font-size: 12px;
    }
    .banner-title {
      position: static;
    }
  }
}
</style>